.container {
    max-width: 1200px;
    margin: auto;
    text-align: center;
    padding: 20px;
  }
  
  /* –°–µ—Ç–∫–∞ —Ä–µ—Ü–µ–ø—Ç–æ–≤ */
  .recipes-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-bottom: 20px;
  }
  
  /* –ö–∞—Ä—Ç–æ—á–∫–∞ —Ä–µ—Ü–µ–ø—Ç–∞ */
  .recipe-card {
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease;
  }
  
  .recipe-card:hover {
    transform: scale(1.05);
  }
  
  /* –ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ä–µ—Ü–µ–ø—Ç–∞ */
  .recipe-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
  }
  
  /* –î–µ—Ç–∞–ª–∏ —Ä–µ—Ü–µ–ø—Ç–∞ */
  .recipe-details {
    max-width: 800px;
    margin: auto;
    text-align: center;
  }
  
  .recipe-details img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  
  /* –ö–Ω–æ–ø–∫–∏ */
  button {
    background-color: #ff6347;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #e5533d;
  }
  

  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  
  .pagination button {
    margin: 0 5px;
    padding: 10px 15px;
    border: 1px solid #ccc;
    background-color: #fff;
    color: #333;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .pagination button.active {
    background-color: #007bff;
    color: white;
    transform: scale(1.05);
  }
  
  .pagination button:disabled {
    cursor: not-allowed;
    background-color: #f1f1f1;
    color: #aaa;
  }
  
  .pagination span {
    margin: 0 5px;
    font-size: 16px;
    color: #333;
  }
  

  .pagination .prev, .pagination .next {
    background-color: #007bff;
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 16px;
    margin: 0 10px;
  }
  
  .pagination .prev:hover, .pagination .next:hover {
    background-color: #0056b3;
  }
  
  .pagination .prev:disabled, .pagination .next:disabled {
    background-color: #f1f1f1;
    color: #aaa;
  }